import React from "react";
import { Link } from "react-router-dom";

import {
  Card,
  CardText,
  NavLink,
  Row,
  Col,
} from "reactstrap";

// core components
import CSNavbar from "components/Navbars/Navbar.js";
import Footer from "components/Footer/Footer.js";


export default function Index() {
  React.useEffect(() => {
    document.body.classList.toggle("landing-page");
    // Specify how to clean up after this effect:
    return function cleanup() {
      document.body.classList.toggle("landing-page");
    };
  },[]);
  return (
    <>
      <CSNavbar />
      <div className="wrapper">
        <div className="page-header">
          <div className="content-center">
            <Row className="row-grid justify-content-between align-items-center">
              <Col>
                <div>
                  <NavLink tag={Link} to="/ship">
                    <Card body outline color="primary">
                      <CardText>Ship List</CardText>
                    </Card>
                  </NavLink>
                </div>
              </Col>
              <Col>
                <div>
                  <NavLink tag={Link} to="/meowfficer">
                    <Card body outline color="primary">
                      <CardText>Meowfficer</CardText>
                    </Card>
                  </NavLink>
                </div>
              </Col>
              <Col>
                <div>
                  <NavLink tag={Link} to="/equipment">
                    <Card body outline color="primary">
                      <CardText>Equipment</CardText>
                    </Card>
                  </NavLink>
                </div>
              </Col>
              <Col>
                <div>
                  <NavLink tag={Link} to="/meowfficer?open=justice">
                    <Card body outline color="primary">
                      <CardText>Blank</CardText>
                    </Card>
                  </NavLink>
                </div>
              </Col>
              <Col>
                <div>
                  <NavLink tag={Link} to="/">
                    <Card body outline color="primary">
                      <CardText>Blank</CardText>
                    </Card>
                  </NavLink>
                </div>
              </Col>
            </Row>
          </div>
        </div>
        <Footer />
      </div>
    </>
  );
}
