import React, { Component } from 'react';

import MeowfficerTalentList from "assets/json/MeowfficerTalentList.json";

export default class MeowfficerTalentTemplate extends Component {
  render() {
  	const talentID = this.props.talentID;

  	let talent = MeowfficerTalentList[0];
    for(var i = 0; i < MeowfficerTalentList.length; i++) {
      if(talentID === MeowfficerTalentList[i].id) {
        talent = MeowfficerTalentList[i];
      }
    }
    const talentName = talent.name.format;
    const talentDesc = talent.desc.format;
  	return (
  	  <tr>
  	  	<th>
  	  	  {talent.image.map((image, imgkey) => {
          const imgkeys = "TalentImageType" + imgkey + talentID;
          return (
            <img 
              key={imgkeys}
              className="img-fluid talentIcon"
              referrerPolicy="no-referrer"
              alt="..."
              src={image} 
            />
          );
        })}
  	  	  <br />{talentName}
  	  	</th>
  	  	<td>{talentDesc}</td>
  	  </tr>
	);
  };
};