import React, { Component } from 'react';
import { Table, Modal, ModalBody } from 'reactstrap';

import MeowfficerTalent from "assets/json/MeowfficerTalent.json";

import MeowfficerSupport from "assets/AL/MeowfficerSupport.png";
import MeowfficerCommand from "assets/AL/MeowfficerCommand.png";
import MeowfficerTactics from "assets/AL/MeowfficerTactics.png";

export default class MeowfficerProfile extends Component {
  render() {
		const modalOpen            = this.props.modalOpen;
		const modalClose           = this.props.modalClose;
		const openSkillModal       = this.props.openSkillModal;
		const profile 			       = this.props.profile;

		// profile
		const meowfficerBanner     = profile.banner;
		const meowfficerName 	   	 = profile.name;
		const meowfficerLogistics  = profile.logistics;
		const meowfficerDirectives = profile.directives;
		const meowfficerTactics    = profile.tactics;
		const meowfficerType       = profile.type;
		const meowfficerSlot       = profile.slot;
		const meowfficerRarity     = profile.rarity;
		const meowfficerFaction    = profile.faction;
		const meowfficerBuildTime  = profile.timer;
		const meowfficerSkill 	   = profile.skill;

		let meowfficer         		 = MeowfficerTalent[0];
	  for(var i = 0; i < MeowfficerTalent.length; i++) {
	    if(MeowfficerTalent[i].name === meowfficerName) {
	      meowfficer = MeowfficerTalent[i];
	     }
	  }

		return (
		  <Modal isOpen={modalOpen} toggle={modalClose} className="meowfficerModal">
		    <ModalBody>
		      <div className="text-center catModalHeader">
		        <img 
		          className="img-fluid"
		          referrerPolicy="no-referrer"
		          alt="..."
		          src={meowfficerBanner}
		        />
		        <br/>
		        <strong>{meowfficerName}</strong>
		      </div>
			    <Table bordered className="catModalTable catModalTableAlign">
			      <thead>
			        <tr>
			          <th>
			            <img src={MeowfficerSupport} className="img-fluid" referrerPolicy="no-referrer" alt="..." />
			            <br />Logistics
			          </th>
			          <th>
			            <img src={MeowfficerCommand} className="img-fluid" referrerPolicy="no-referrer" alt="..." />
			            <br />Directives
			          </th>
			          <th>
			            <img src={MeowfficerTactics} className="img-fluid" referrerPolicy="no-referrer" alt="..." />
			            <br />Tactics
			          </th>
			        </tr>
			      </thead>
		        <tbody>
		          <tr>
		            <td>{meowfficerLogistics }</td>
		            <td>{meowfficerDirectives}</td>
		            <td>{meowfficerTactics   }</td>
		          </tr>
		          <tr>
		          	<th colSpan="3">Talent</th>
		          </tr>
		          {meowfficer.talent.map((talent, key) => {
		          	const talentKeys 			= meowfficerName + "Talent" + key;
		          	const talentType			= talent.type;
		          	const talentPrimary	 	= talent.primary;
		          	const talentSecondary = talent.secondary;
		          	const talentOther     = talent.other;
		          	const openTalentModal = () => {
		          		this.props.openTalentModal({
		          			talentType     : talentType,
		          			talentPrimary	 : talentPrimary,
		          			talentSecondary: talentSecondary,
		          			talentOther 	 : talentOther,
		          		})
		          	}
		          	return (
		          		<tr key={talentKeys} onClick={openTalentModal}>
				          	<th colSpan="3" className="linkCS">{talentType}</th>
				          </tr>
		          	);
		          })}
		          <tr onClick={openSkillModal}>
		            <th>Skill</th>
		            <th colSpan="2" className="linkCS">{meowfficerSkill}</th>
		          </tr>
		          <tr>
		            <th>Skill Type</th>
		            <td colSpan="2">{meowfficerType}</td>
		          </tr>
		          <tr>
		            <th>Skill Slot</th>
		            <td colSpan="2">{meowfficerSlot}</td>
		          </tr>
		          <tr>
		            <th>Rarity</th>
		            <td colSpan="2">{meowfficerRarity}</td>
		          </tr>
		          <tr>
		            <th>Faction</th>
		            <td colSpan="2">{meowfficerFaction}</td>
		          </tr>
		          <tr>
		            <th>Training Time</th>
		            <td colSpan="2">{meowfficerBuildTime}</td>
		          </tr>
		        </tbody>
		      </Table>
		    </ModalBody>
		  </Modal>
		)
  }
}