import React, { Component } from 'react';
import { Table, Modal, ModalBody } from 'reactstrap';

export default class MeowfficerSkill extends Component {
  render() {
  	const skillModalOpen		    = this.props.skillModalOpen;
	  const skillModalClose		    = this.props.skillModalClose;
	  const skillData 			      = this.props.skill
  
	  const meowfficerSkillImage  = skillData.image;
	  const meowfficerSkill		    = skillData.name;
	  const meowfficerSkill1		  = skillData.general.level1;
	  const meowfficerSkill2		  = skillData.general.level2;
	  const meowfficerSkill3		  = skillData.general.level3;
	  const meowfficerOpsi1		    = skillData.opsi.level1;
	  const meowfficerOpsi2		    = skillData.opsi.level2;
	  const meowfficerOpsi3		    = skillData.opsi.level3;
	
    return (
      <Modal isOpen={skillModalOpen} toggle={skillModalClose} size="xl" className="MeowfficerSkillModal">
        <ModalBody>
          <div className="text-center catModalHeader font16px">
            <img 
              className="img-fluid"
              referrerPolicy="no-referrer"
              alt="..."
              src={meowfficerSkillImage}
            />
            <br/>
            <strong>{meowfficerSkill}</strong>
          </div>
          <Table bordered className="catModalTable catSkillModalTable">
            <thead>
              <tr>
                <th colSpan="3" className="font14px bc-mediumslateblue">Normal</th>
              </tr>
              <tr className="catSkillWidth">
                <th className="font13px">Level</th>
                <th className="font13px">Conditions</th>
                <th className="font13px">Effect</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <th>1</th>
                <td>{meowfficerSkill1[0]}</td>
                <td>{meowfficerSkill1[1]}</td>
              </tr>
              <tr>
                <th>2</th>
                <td>{meowfficerSkill2[0]}</td>
                <td>{meowfficerSkill2[1]}</td>
              </tr>
              <tr>
                <th>3</th>
                <td>{meowfficerSkill3[0]}</td>
                <td>{meowfficerSkill3[1]}</td>
              </tr>
            </tbody>
            <thead>
              <tr>
                <th colSpan="3" className="font14px bc-mediumslateblue">Operation Siren</th>
              </tr>
              <tr>
                <th className="font13px">Level</th>
                <th className="font13px">Conditions</th>
                <th className="font13px">Effect</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <th>1</th>
                <td>{meowfficerOpsi1[0]}</td>
                <td>{meowfficerOpsi1[1]}</td>
              </tr>
              <tr>
                <th>2</th>
                <td>{meowfficerOpsi2[0]}</td>
                <td>{meowfficerOpsi2[1]}</td>
              </tr>
              <tr>
                <th>3</th>
                <td>{meowfficerOpsi3[0]}</td>
                <td>{meowfficerOpsi3[1]}</td>
              </tr>
            </tbody>
          </Table>
        </ModalBody>
      </Modal>
	)
  }
}