import React, { useState, useEffect } from "react";
import { Switch, Route, Link } from "react-router-dom";

import Ship from "views/Ship.js";

// core components
import CSNavbar from "components/Navbars/Navbar.js";
import Footer from "components/Footer/Footer.js";


function ShipList() {
  const [error, setError] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [azurAPI, setAzurAPI] = useState([]);

  useEffect(() => {
    fetch("https://raw.githubusercontent.com/AzurAPI/azurapi-js-setup/master/ships.json")
      .then(res => res.json())
      .then(
        (result) => {
          setIsLoaded(true);
          setAzurAPI(result);
        },
        (error) => {
          setIsLoaded(true);
          setError(error);
        }
      )
  }, [])

  if (error) {
    return <div>Error: {error.message}</div>;
  } else if (!isLoaded) {
    return <div>Loading...</div>;
  } else {
    return (
      <>
        <Switch>
          <Route path="/ship/:id" render={({ match }) => {
            const id = match.params.id
            const shipData = azurAPI.find(ship => encodeURIComponent(escape(ship.names.en.toLowerCase())) === id)
            return <Ship shipData={shipData} />
          }} />
          <Route path="/ship" render={() => {
            return (
              <>
                <CSNavbar />
                <div className="wrapper">
                  <div className="page-header shipheader">
                    <div className="content">
                      <h1>Ship List</h1>
                      <div className="containerCS">
                        {azurAPI.map((ship) => {
                          return (
                            <div className="cardCS shipCard" key={ship._gid}>
                              <Link to={`/ship/${encodeURIComponent(escape(ship.names.en.toLowerCase()))}`} className="shipName linkCS">
                                {ship.names.en}
                              </Link >
                            </div>
                          )
                        })}
                      </div>
                    </div>
                  </div>
                  <Footer />
                </div>
              </>
            )
          }} />
        </Switch>
      </>
    );
  }
}

export default ShipList;
