import React, { Component } from "react";

import MeowfficerData from "assets/json/MeowfficerData.json";

export default class MeowfficerList extends Component {
  render() {
    let idx = this.props.codex;
    if(idx === null || idx === undefined) {idx = 0}

    const meowfficer  = MeowfficerData[idx];
    const catColor    = "cat" + meowfficer.color;
    const catName     = meowfficer.name;
    const catIcon     = meowfficer.icon;

    const openModal   = () => {
      this.props.openModal({
        name  : catName,
        codex : idx
      })
    };
    return (
      <div className="cardCS" onClick={openModal}>
        <div className="meowfficerBorder">
          <img src={catIcon} className={"meowfficerIcon img-fluid " + catColor} referrerPolicy="no-referrer" alt="..." />
        </div>
        <div className="align-middle meowfficerName">{catName}</div>
      </div>
    )
  };
};