import React, { useState } from "react";

// core components
import CSNavbar from "components/Navbars/Navbar.js";
import Footer from "components/Footer/Footer.js";

// meowfficer component
import MeowfficerAllTalentModal from "views/components/Meowfficer/MeowfficerAllTalentModal";
import MeowfficerList from "views/components/Meowfficer/MeowfficerList";
import MeowfficerModal from "views/components/Meowfficer/MeowfficerModal";

// json
import MeowfficerData from "assets/json/MeowfficerData.json";
import MeowfficerTalent from "assets/json/MeowfficerTalent.json";

function Meowfficer() {
  const [modal, setModal]            = useState(false);
  const [skillModal, setSkillModal]  = useState(false);
  const [allTalent, setAllTalent]    = useState(false);
  const [talentModal, setTalentModal]= useState(false);

  const [modalData, setModalData]    = useState({
    name      : null,
    codex     : null,
    talentType: null,
    primary   : null,
    secondary : null,
    other     : null,
  });

  const [talentData, setTalentData]  = useState({
    talentType: null,
    primary   : null,
    secondary : null,
    other     : null,
  });

  // profile modal
  const openModal = (cat) => {
    setModalData({
      name  : cat.name ,
      codex : cat.codex,
    });
    setModal(true);
    window.history.replaceState(null, null, 
      "#/meowfficer?open=" + cat.name.toLowerCase()
    );
  };

  const closeModal = () => {
    setModal(false);
    window.history.replaceState(null, null, 
      "#/meowfficer"
    );
  };

  // skill modal
  const openSkillModal = () => {
    setSkillModal(true);
    window.history.replaceState(null, null,
      "#/meowfficer?open=" + escape(modalData.name.toLowerCase()) + "=skill"
    );
  }

  const closeSkillModal = () => {
    setSkillModal(false);
    window.history.replaceState(null, null,
      "#/meowfficer?open=" + escape(modalData.name.toLowerCase())
    );
  }

  // all talent modal
  const openAllTalent = () => {
    setAllTalent(true);
    window.history.replaceState(null, null, 
      "#/meowfficer?open=AllTalentList"
    );
  }

  const closeAllTalent = () => {
    setAllTalent(false);
    window.history.replaceState(null, null, 
      "#/meowfficer"
    );
  }

  // talent modal
  const openTalentModal = (talent) => {
    setTalentData({
      talentType: talent.talentType,
      primary   : talent.talentPrimary,
      secondary : talent.talentSecondary,
      other     : talent.talentOther,
    });
    setTalentModal(true);
    const talentName = escape(talent.talentType.toLowerCase().replace(/\s+/g, "_"))
    window.history.replaceState(null, null,
      "#/meowfficer?open=" + escape(modalData.name.toLowerCase()) + "=talent?=" + talentName 
    );
  }

  const closeTalentModal = () => {
    setTalentModal(false);
    window.history.replaceState(null, null,
      "#/meowfficer?open=" + escape(modalData.name.toLowerCase())
    );
  }

  React.useEffect(() => {
    try {
      const urlink = window.location.href.split("=");
      if (urlink.length > 1) {
        let cat   = "";
        let codex = 0;

        for(var i = 0; i < MeowfficerData.length; i++) {
          if(MeowfficerData[i].name.toLowerCase() === urlink[1]) {
            cat   = MeowfficerData[i];
            codex = i;
          }
        }
        setModalData({
          name : cat.name,
          codex: codex,
        })
        setModal(true);
        if(urlink[1] === "AllTalentList") {setAllTalent(true) }
        if(urlink.length === 3)           {setSkillModal(true)}
        if(urlink.length === 4) {
          const talent = MeowfficerTalent[codex];
          const talentCompare1 = escape(talent.talent[0].type.toLowerCase().replace(/\s+/g, "_"))
          let talentCompare2 = "";
          if(talent.talent.length === 2) {
            talentCompare2 = escape(talent.talent[1].type.toLowerCase().replace(/\s+/g, "_"))
          }
          if(urlink[3] === talentCompare1) {
            setTalentData({
              talentType: talent.talent[0].type,
              primary   : talent.talent[0].primary,
              secondary : talent.talent[0].secondary,
              other     : talent.talent[0].other,
            });
          }
          if(urlink[3] === talentCompare2) {
            setTalentData({
              talentType: talent.talent[1].type,
              primary   : talent.talent[1].primary,
              secondary : talent.talent[1].secondary,
              other     : talent.talent[1].other,
            });
          }
          setTalentModal(true);
        }
      }
    } catch (e) {
      console.log("Meowfficer not found.");
      window.history.replaceState(null, null, "#/meowfficer");
    }
  }, []);

  return (
    <>
      <CSNavbar />
      <div className="wrapper">
        <div className="page-header meowfficerheader">
          <div className="content">
            <h1>Meowfficer</h1>
            <MeowfficerAllTalentModal 
              openAllTalent ={openAllTalent }
              allTalentOpen ={allTalent     }
              allTalentClose={closeAllTalent}
            />
            <div className="containerCS">
              {MeowfficerData.map((cat, key) => {
                const name = cat.name;
                const keys = "List" + key + name
                return (
                  <MeowfficerList
                    key       ={keys     }
                    codex     ={key      }
                    openModal ={openModal}
                    name      ={name     }
                  />
                );
              })}
            </div>
            <MeowfficerModal 
              modalData       ={modalData       }
              modalOpen       ={modal           }
              modalClose      ={closeModal      }
              openSkillModal  ={openSkillModal  }
              skillModalOpen  ={skillModal      }
              skillModalClose ={closeSkillModal }     
              openTalentModal ={openTalentModal }
              talentData      ={talentData      }
              talentModalOpen ={talentModal     }
              talentModalClose={closeTalentModal}
            />
          </div>
        </div>
        <Footer />
      </div>
    </>
  );
}

export default Meowfficer;
