import React, { Component } from 'react';
import { Table, Modal, ModalBody } from 'reactstrap';

import MeowfficerTalentTemplate from "views/components/Meowfficer/MeowfficerTalentTemplate";

export default class MeowfficerTalentModal extends Component {
  render() {
	  const talentModalOpen	 = this.props.talentModalOpen;
	  const talentModalClose = this.props.talentModalClose;

    let talentType         = this.props.talent.talentType;
    let talentPrimary      = this.props.talent.primary;
    let talentSecondary    = this.props.talent.secondary;
    let talentOther        = this.props.talent.other;

    if(talentType === null || talentType === undefined) {
      talentType      = "Default";
      talentPrimary   = ["flame"];
      talentSecondary = ["flame"];
      talentOther     = ["flame"];
    }
    return (
      <Modal isOpen={talentModalOpen} toggle={talentModalClose} size="xl" className="MeowfficerTalentModal">
        <ModalBody>
          <Table bordered className="catModalTable">
            <thead>
              <tr>
                <th colSpan="2" className="font16px bc-palegoldenrod">{talentType}</th>
              </tr>
              <tr>
                <th colSpan="2" className="font14px bc-mediumslateblue">Primary</th>
              </tr>
            </thead>
            <tbody>
              {talentPrimary.map((primary, primaryKey) => {
                const primaryKeys = primaryKey + talentType + "Primary";
                return (
                  <MeowfficerTalentTemplate
                    key     ={primaryKeys} 
                    talentID={primary    }
                  />
                );
              })}
            </tbody>
            <thead>
              <tr>
                <th colSpan="2" className="font14px bc-mediumslateblue">Secondary</th>
              </tr>
            </thead>
            <tbody>
              {talentSecondary.map((secondary, secondaryKey) => {
                const secondaryKeys = secondaryKey + talentType + "Secondary";
                return (
                  <MeowfficerTalentTemplate
                    key     ={secondaryKeys} 
                    talentID={secondary    }
                  />
                );
              })}
            </tbody>
            <thead>
              <tr>
                <th colSpan="2" className="font14px bc-mediumslateblue">Other</th>
              </tr>
            </thead>
            <tbody>
              {talentOther.map((other, otherKey) => {
                const otherKeys = otherKey + talentType + "Other";
                return (
                  <MeowfficerTalentTemplate
                    key     ={otherKeys} 
                    talentID={other    }
                  />
                );
              })}
            </tbody>
          </Table>
        </ModalBody>
      </Modal>
	  )
  }
}