import React, { Component } from "react";
import { Button, Modal, ModalBody, Table } from "reactstrap";

import MeowfficerTalentList from "assets/json/MeowfficerTalentList.json"

export default class MeowfficerAllTalentModal extends Component {
  render() {
    const openAllTalent   = this.props.openAllTalent;
    const allTalentOpen   = this.props.allTalentOpen;
    const allTalentClose  = this.props.allTalentClose;
    return (
      <>
	      <Button color="primary" block onClick={openAllTalent}>Talent List</Button>
        <Modal isOpen={allTalentOpen} toggle={allTalentClose} size="xl" className="MeowfficerAllTalentModal">
          <ModalBody>
            <Table bordered className="catModalTable">
              <thead>
                <tr>
                  <th colSpan="2">All Talent List</th>
                </tr>
              </thead>
              <tbody>
                {MeowfficerTalentList.map((talent, key) => {
                  const name  = talent.name.format;
                  const desc  = talent.desc.format;
                  const idx   = key;
                  const ids   = talent.id;
                  const keys  = "talent" + key + name;
                  return (
                    <tr key={keys}>
                      <th>
                        {MeowfficerTalentList[idx].image.map((image, imgkey) => {
                          const imgkeys = "TalentImage" + imgkey + ids;
                          return (
                            <img 
                              key={imgkeys}
                              className="img-fluid talentIcon"
                              referrerPolicy="no-referrer"
                              alt="..."
                              src={image} 
                            />
                          );
                        })}
                        <br/>{name}
                      </th>
                      <th>{desc}</th>
                    </tr>
                  )
                })}
              </tbody>
            </Table>
          </ModalBody>
        </Modal>
      </>
  	)
  };
};